@circle-width: unit(round(250/@base-px-2-rem, 2), rem);
@circle-color: rgb(199, 205, 193);
@circle-color-bg: rgb(0, 0, 0);
//订单详情页
//倒计时环形

.progress-circle {
  margin-top: unit(round(40/@base-px-2-rem, 2), rem);
  width: @circle-width;
  height: @circle-width;
  background-color: @circle-color-bg;
  position: relative;

  //border: 3px solid #111111;
  border-radius: 50%;
  & p {
    &:first-child {
      font-weight: 600;
      font-size: 18px;
      color: #111;
    }

    &:last-child {
      font-size: 10px;
      color: @border-color-gray;
    }
  }

}

.progress-circle:after {
  border: none;
  position: absolute;
  top: unit(round(15/@base-px-2-rem, 3), rem);
  left: unit(round(15/@base-px-2-rem, 3), rem);
  text-align: center;
  display: block;
  border-radius: 50%;
  width: unit(round(220/@base-px-2-rem, 3), rem);
  height: unit(round(220/@base-px-2-rem, 3), rem);
  background-color: white;
  content: " ";
}

/* Text inside the control */
.progress-circle .progress-circle-text {
  position: absolute;
  top: unit(round(75/@base-px-2-rem, 3), rem);
  line-height: unit(round(60/@base-px-2-rem, 3), rem);
  width: @circle-width;
  text-align: center;
  display: block;
  color: #53777A;
  z-index: 2;
}

.left-half-clipper {
  /* a round circle */
  border-radius: 50%;
  width: @circle-width;
  height: @circle-width;
  position: absolute; /* needed for clipping */
  clip: rect(0, @circle-width, @circle-width, unit(round(125/@base-px-2-rem,3), rem)); /* clips the whole left half*/
}

/* when p>50, don't clip left half*/
.progress-circle.over50 .left-half-clipper {
  clip: rect(auto, auto, auto, auto);
}

.value-bar {
  /*This is an overlayed square, that is made round with the border radius,
  then it is cut to display only the left half, then rotated clockwise
  to escape the outer clipping path.*/
  position: absolute; /*needed for clipping*/
  clip: rect(0, unit(round(125/@base-px-2-rem,3), rem), @circle-width, 0);
  width: @circle-width;
  height: @circle-width;
  border-radius: 50%;
  border: 0.7em solid @circle-color; /*The border is 0.35 but making it larger removes visual artifacts */
  /*background-color: #4D642D;*/ /* for debug */
  box-sizing: border-box;

}

/* Progress bar filling the whole right half for values above 50% */
.progress-circle.over50 .first50-bar {
  /*Progress bar for the first 50%, filling the whole right half*/
  position: absolute; /*needed for clipping*/
  clip: rect(0, @circle-width, @circle-width, unit(125/@base-px-2-rem, rem));
  background-color: #53777A;
  border-radius: 50%;
  width: @circle-width;
  height: @circle-width;
}

.progress-circle:not(.over50) .first50-bar {
  display: none;
}

/* Progress bar rotation position */
