加在完毕文字提示

******************/

//直接用于任何容器 已具有样式
p.end-text {
  color: #999;
  position: relative;
  width: 100%;
  text-align: center;
  font-size: 15px;
  background-color: #fff;
  letter-spacing: 1px;
  font-weight: 500;
  .make-line-and-height-p(@big-banner-text-height);
  .mod-retina-display(20px, 2px);
}

//购物车猜你喜欢 两条线
.shopcar, .arrival, .wish, .collect {
  .base-mt-wrapper {
    .base-title(line);
    > p.guess-you-like {
      color: #aaa;
      font-size: 16px;
    }
  }
}

.product-search-wrapper {
  display: none;
  background: #fff;
}

//404 页面
.error-wrapper {
  background-color: @blue;
  padding: unit(round(150/@base-px-2-rem, 1), rem) 0;
  height: 100%;
  color: #fff;
  img {
    width: 100%;
  }
  p {
    text-align: center;
    font-size: 20px;
    color: @dark-blue;
    text-shadow: 2px 2px 0 @light-blue;
  }

}

